@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Mono';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* 
solved margin auto main content */
html {
    overflow-y:scroll;
}

.app-section {
    padding: 10px;
}

.app-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.app-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 30px;
    -webkit-column-gap: 30px;
            column-gap: 30px;
    grid-row-gap: 30px;
    row-gap: 30px;
}






.anime-img {
    display: block;
    max-width: 100%;
    cursor: pointer;
}

.anime-item  h4{
    text-align: center;
}

@media only screen and (max-width: 460px) {
    .anime-item  h4{
        font-size: 13px;
    }
  }
.anime-post {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.anime-post-bg {
    background-color: rgba(0, 0, 0, 0.808);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.anime-post-content {
    position: relative;
    max-width: 600px;
    background-color: rgba(255, 255, 255, 0.89);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin: 50px;
}

.anime-post-content img {
    display: block;
    max-width: 100%;
    max-height: calc(100vh - 120px);
    margin: 0 auto;

}

.anime-post-content h4 {
    padding: 5px;
    text-align: center;
    color: rgb(0, 0, 0);
}

.app-header {
    border-bottom:1px solid gray;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app-header-logo {
    max-width: 60px;
    margin-right: 10px;
}
.app-search {
    margin-bottom: 20px;
}

.app-search-input {
    font-family: 'Roboto Mono';
    width: 100%;
    padding: 8px;
    font-size: 16px;
    box-sizing: border-box;
}
