.anime-post {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.anime-post-bg {
    background-color: rgba(0, 0, 0, 0.808);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.anime-post-content {
    position: relative;
    max-width: 600px;
    background-color: rgba(255, 255, 255, 0.89);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    margin: 50px;
}

.anime-post-content img {
    display: block;
    max-width: 100%;
    max-height: calc(100vh - 120px);
    margin: 0 auto;

}

.anime-post-content h4 {
    padding: 5px;
    text-align: center;
    color: rgb(0, 0, 0);
}
