/* 
solved margin auto main content */
html {
    overflow-y:scroll;
}

.app-section {
    padding: 10px;
}

.app-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
}

.app-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 30px;
}





